
.footer{
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100px;
    height: auto;
    flex-wrap: wrap;
    color: #fff;
    background-color: #000;
    padding: 20px 10px;
    margin-top: 30px;

    .footer-left{
        // flex: 1;
        text-align: center;
        
        span{
            color:red
        }
    }

    .footer-right{
        flex: 1;
        display: flex;
        justify-content: center;
        align-items: center;

        img{
            width: 30px;
            height: 30px;
            margin: 0px 10px;
        }
    }
}

@media screen and (max-width:480px) {
    
    .footer{
    flex-direction: column;
    }
    
}