.about-container{

    position: relative;
    min-height: auto;
    min-height: 600px;

    .planet{
        z-index: 0;
        position: absolute;
        opacity: 0.9;
        &:nth-of-type(4){
            transform: scale(5);
            left: 5% ;
            top: 35%;
        }
        &:nth-of-type(2){
            transform: scale(2);
            left:  30% ;
            top: 70%;
        }
        &:nth-of-type(3){
            transform: scale(3.5);
            right: 10% ;
            top: 95%;
        }
    }

    .row{
        z-index: 4 ;

    }
.about-text{
    margin-top: 100px;
    z-index: 56;
    width: 100%;
    height: auto;

    h4{
        color: white;
        font-size: 4.5rem;
        word-spacing:4em;
    }
    .description p{
        color: var(--color_grey);
        font-size: 1.0rem;
        font-weight: 100;
        margin-left: 40px;

    }


}

.about-left-ani{
    position: relative;

 
    span{
        font-size: 20rem;
        position: absolute;
        z-index: 0;
        left: 20%;
    }
    span::after{
        content: 'S';
        z-index: 2;
    }

    span:nth-child(1){
        transform:translateX(10px);
    }
    span:nth-child(2){
        transform : translateX(25px);
        color : var(--color_purple);
    }
    span:nth-child(3){
        transform:translateX(20px );
        color: var(--color_grey);
    }
    span:nth-child(4){
        // transform:translateX(20px );
        color: var(--pr_bg);
    }
       
// @for $i from 1 through 4 {

//     span:nth-child(#{$i}){
//        translateY( calc( -10px  -  #{$i}px ));
        
//   }

  
      
//     }

    


}

}

@media (max-width : 800px) {
    .about-container{
        .planet{

            &:nth-of-type(4){
                transform: scale(2);
                left: 5% ;
                top: 35%;
            }
            &:nth-of-type(2){
                transform: scale(1);
                left:  30% ;
                top: 70%;
            }
            &:nth-of-type(3){
                transform: scale(0.8);
                right: 10% ;
                top: 95%;
            }
        }
    }   

}
